.App {
	display: grid;
	grid-template-rows: 120px 1fr;
	min-height: 100vh;
	transition: all 0.5s ease-in-out;
}

body:before {
	content: "";
	display: block;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: -10;
	background: url(/long-shot-league/static/media/breslin.32708196.jpeg) no-repeat center center;
	background-size: cover;
}

